:root {
    --main-color: rgb(183, 235, 207);
    --main-color-light: rgb(199, 255, 225);
    --secondary-color: rgb(235, 132, 14);
    --secondary-color-dark: rgb(189, 112, 24);
    --warning-color: rgb(185, 20, 20);
    --warning-color-dark: rgb(160, 21, 21);
    --green-border: 1px solid rgb(192, 238, 212);
    --gray-border: 1px solid rgb(221, 221, 221);
    --small-radius: 8px;
    --large-radius: 15px;
    --black-text: rgb(25, 25, 25);
    --off-white: rgb(245, 245, 245);
    --disabled-button-color: rgb(200, 200, 200);
    --card-shadow: 0px 1px 4px #7ca58f;
    --list-shadow: inset 0px 0px 4px #7ca58f;
    --screen-shadow: 0px 1px 4px rgb(46, 66, 55);
    --screen-shade: rgba(61, 87, 72, 0.9);
  }